import React from 'react'

import Radio from './styled'

export default props => {
  const {
    options,
    selectedValue,
    onChange
  } = props

  return <Radio>
    {options.map(option => <Radio.Item
      key={option.value}
      onClick={selectedValue === option.value ? undefined: () => onChange(option.value)}
      active={selectedValue === option.value}
    >
      <Radio.Icon active={selectedValue === option.value} />

      <span>{option.label}</span>
    </Radio.Item>)}
  </Radio>
}