import update from 'immutability-helper'
import { cloneDeep } from 'lodash-es'

import * as imageActionTypes from 'constants/image'

export default (
  state = {
    items: [],
    filteredItems: [],
    isLoading: false,
    isError: false,
    hasData: false
  },
  action
) => {
  const { type, payload } = action

  switch (type) {
    case imageActionTypes.SEARCH_REQUEST: {
      return update(state, {
        isLoading: { $set: true }
      })
    }

    case imageActionTypes.SEARCH_SUCCESS: {
      return update(state, {
        $merge: {
          items: payload,
          isLoading: false,
          hasData: true
        }
      })
    }

    case imageActionTypes.SEARCH_FAILURE: {
      return update(state, {
        isLoading: { $set: false },
        isError: { $set: true }
      })
    }

    case imageActionTypes.MARK_AS_FAVORITE: {
      return update(state, {
        items: prevItems => {
          const foundItemIndex = prevItems.findIndex(item => item.id === payload.imageId)

          return update(prevItems, {
            [foundItemIndex]: {
              isFavorite: { $set: true }
            }
          })
        }
      })
    }

    case imageActionTypes.REMOVE_FAVORITE: {
      return update(state, {
        items: prevItems => {
          const foundItemIndex = prevItems.findIndex(item => item.id === payload.imageId)

          return update(prevItems, {
            [foundItemIndex]: {
              isFavorite: { $set: false }
            }
          })
        }
      })
    }

    case imageActionTypes.REMOVE: {
      return update(state, {
        items: prevItems => {
          const foundItemIndex = prevItems.findIndex(item => item.id === payload.imageId)

          return update(prevItems, {
            $splice: [ [ foundItemIndex, 1 ] ]
          })
        }
      })
    }

    case imageActionTypes.UPDATE: {
      return update(state, {
        items: prevItems => {
          const foundItemIndex = prevItems.findIndex(item => item.id === payload.imageId)

          return update(prevItems, {
            [foundItemIndex]: { $merge: payload.data }
          })
        }
      })
    }

    case imageActionTypes.FILTER: {
      return update(state, {
        filteredItems: () => {
          let filteredItems = cloneDeep(state.items)

          switch (payload.sortType) {
            case 'oldest': {
              filteredItems.sort((a , b) => {
                if (a.dateCreated > b.dateCreated) {
                  return 1
                }

                if (a.dateCreated < b.dateCreated) {
                  return -1
                }

                return 0
              })

              break
            }

            case 'titleAZ': {
              filteredItems.sort((a , b) => {
                if (a.title.toLowerCase() > b.title.toLowerCase()) {
                  return 1
                }

                if (a.title.toLowerCase() < b.title.toLowerCase()) {
                  return -1
                }

                return 0
              })

              break
            }

            case 'titleZA': {
              filteredItems.sort((a , b) => {
                if (a.title.toLowerCase() < b.title.toLowerCase()) {
                  return 1
                }

                if (a.title.toLowerCase() > b.title.toLowerCase()) {
                  return -1
                }

                return 0
              })

              break
            }

            default: {
              filteredItems.sort((a , b) => {
                if (a.dateCreated < b.dateCreated) {
                  return 1
                }

                if (a.dateCreated > b.dateCreated) {
                  return -1
                }

                return 0
              })
            }
          }

          return filteredItems
            .slice((payload.page - 1) * payload.itemPerPage, payload.itemPerPage)
        }
      })
    }

    default: {
      return state
    }
  }
}