import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import queryString from 'query-string'

// Components
import ImageCard from 'components/ImageCard'
import { Row, Col } from 'reactstrap'
import Wrapper from './components/Wrapper'
import ImageListWrapper from 'components/ImageListWrapper'
import Filter from 'containers/Filter'

// Selectors
import { filteredItemsSelector } from 'selectors/image'

// Actions
import { filter } from 'actions/image'

const Home = props => {
  const {
    hasData,
    images,
    location,
    filter
  } = props

  const currentQuery = queryString.parse(location.search)

  useEffect(() => {
    filter(currentQuery)
  }, [ location ])

  return <Wrapper>
    <Row>
      <Col xs={3}>
        <Filter
          sortType={currentQuery.sortType || 'newest'}
        />
      </Col>

      <Col xs={9}>
        <ImageListWrapper>
          <Row>
            {images.map(image => <Col key={image.id} md={4}>
              <ImageCard {...image} />
            </Col>)}
          </Row>
        </ImageListWrapper>
      </Col>
    </Row>
  </Wrapper>
}

const mapStateToProps = state => ({
  hasData: state.image.hasData,
  images: filteredItemsSelector(state)
})

const mapDispatchToProps = {
  filter
}

export default connect(mapStateToProps, mapDispatchToProps)(Home)