import styled from 'styled-components'
import is from 'styled-is'

const Radio = styled.div`
  margin-top: 17px;
`

Radio.Icon = styled.div`
  width: 20px;
  height: 20px;
  border: 1px solid white;
  border-radius: 50%;
  position: relative;
  margin-right: 7px;

  &:before {
    content: '';
    position: absolute;
    top: 5px;
    left: 5px;
    width: 8px;
    height: 8px;
    background: white;
    border-radius: 50%;
  }
`

Radio.Item = styled.div`
  display: flex;
  align-items: center;
  color: white;
  cursor: pointer;
  user-select: none;
  padding-left: 8px;

  ${is('active')`
    ${Radio.Icon} {
      border-color: #a38fc4;

      &:before {
        background: #a38fc4;
      }
    }

    span {
      color: #a38fc4;
    }
  `}

  &:not(:last-child) {
    margin-bottom: 8px;
  }
`

export default Radio